// DeepSeek API 
export interface DeepSeekMessage {
  role: 'system' | 'user' | 'assistant';
  content: string;
}

export interface DeepSeekResponse {
  id: string;
  object: string;
  created: number;
  model: string;
  choices: {
    index: number;
    message: {
      role: string;
      content: string;
    };
    finish_reason: string;
  }[];
  usage: {
    prompt_tokens: number;
    completion_tokens: number;
    total_tokens: number;
  };
}

class DeepSeekAPI {
  private apiKey: string;
  private baseURL: string = 'https://api.deepseek.com/v1';

  constructor(apiKey: string) {
    this.apiKey = apiKey;
  }

  async chat(messages: DeepSeekMessage[], options?: {
    model?: string;
    temperature?: number;
    max_tokens?: number;
    stream?: boolean;
  }): Promise<string> {
    try {
      const response = await fetch(`${this.baseURL}/chat/completions`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.apiKey}`,
        },
        body: JSON.stringify({
          model: options?.model || 'deepseek-chat',
          messages,
          temperature: options?.temperature || 0.7,
          max_tokens: options?.max_tokens || 1000,
          stream: options?.stream || false,
        }),
      });

      if (!response.ok) {
        throw new Error(`DeepSeek API error: ${response.status} ${response.statusText}`);
      }

      const data: DeepSeekResponse = await response.json();
      
      if (data.choices && data.choices.length > 0) {
        return data.choices[0].message.content;
      }
      
      throw new Error('No response from DeepSeek API');
    } catch (error) {
      console.error('DeepSeek API Error:', error);
      throw error;
    }
  }

  async streamChat(
    messages: DeepSeekMessage[], 
    onChunk: (chunk: string) => void,
    options?: {
      model?: string;
      temperature?: number;
      max_tokens?: number;
    }
  ): Promise<void> {
    try {
      const response = await fetch(`${this.baseURL}/chat/completions`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.apiKey}`,
        },
        body: JSON.stringify({
          model: options?.model || 'deepseek-chat',
          messages,
          temperature: options?.temperature || 0.7,
          max_tokens: options?.max_tokens || 1000,
          stream: true,
        }),
      });

      if (!response.ok) {
        throw new Error(`DeepSeek API error: ${response.status} ${response.statusText}`);
      }

      const reader = response.body?.getReader();
      if (!reader) {
        throw new Error('Failed to get response reader');
      }

      const decoder = new TextDecoder();
      let buffer = '';

      while (true) {
        const { done, value } = await reader.read();
        if (done) break;

        buffer += decoder.decode(value, { stream: true });
        const lines = buffer.split('\n');
        buffer = lines.pop() || '';

        for (const line of lines) {
          if (line.startsWith('data: ')) {
            const data = line.slice(6);
            if (data === '[DONE]') {
              return;
            }

            try {
              const parsed = JSON.parse(data);
              const content = parsed.choices?.[0]?.delta?.content;
              if (content) {
                onChunk(content);
              }
            } catch (e) {
              // 
            }
          }
        }
      }
    } catch (error) {
      console.error('DeepSeek Stream API Error:', error);
      throw error;
    }
  }
}

//  DeepSeek API 
let deepSeekInstance: DeepSeekAPI | null = null;

export const initializeDeepSeek = (apiKey: string) => {
  deepSeekInstance = new DeepSeekAPI(apiKey);
  return deepSeekInstance;
};

export const getDeepSeekInstance = (): DeepSeekAPI | null => {
  return deepSeekInstance;
};

//  AI 
export class FitnessCoachAI {
  private deepSeek: DeepSeekAPI;
  private coachPersonality: 'strict' | 'gentle' | 'humorous';
  private userContext: {
    name: string;
    goals: string[];
    currentProgress: string;
    todayCheckIns: number;
    totalCheckIns: number;
  };

  constructor(
    deepSeek: DeepSeekAPI, 
    personality: 'strict' | 'gentle' | 'humorous',
    userContext: any
  ) {
    this.deepSeek = deepSeek;
    this.coachPersonality = personality;
    this.userContext = userContext;
  }

  private getSystemPrompt(): string {
    const personalityPrompts = {
      strict: `
- 
- 
- 
- 
- """"`,
      
      gentle: `
- 
- 
- 
- 
- """"`,
      
      humorous: `
- 
- 
- 
- 
- `
    };

    return `${personalityPrompts[this.coachPersonality]}


- ${this.userContext.name}
- ${this.userContext.goals.join('')}
- ${this.userContext.currentProgress}
- ${this.userContext.todayCheckIns}/${this.userContext.totalCheckIns}

100`;
  }

  async getResponse(userMessage: string, conversationHistory: DeepSeekMessage[] = []): Promise<string> {
    const messages: DeepSeekMessage[] = [
      { role: 'system', content: this.getSystemPrompt() },
      ...conversationHistory,
      { role: 'user', content: userMessage }
    ];

    try {
      const response = await this.deepSeek.chat(messages, {
        temperature: 0.8,
        max_tokens: 200
      });
      return response;
    } catch (error) {
      console.error('AI:', error);
      return this.getFallbackResponse(userMessage);
    }
  }

  async getStreamResponse(
    userMessage: string, 
    onChunk: (chunk: string) => void,
    conversationHistory: DeepSeekMessage[] = []
  ): Promise<void> {
    const messages: DeepSeekMessage[] = [
      { role: 'system', content: this.getSystemPrompt() },
      ...conversationHistory,
      { role: 'user', content: userMessage }
    ];

    try {
      await this.deepSeek.streamChat(messages, onChunk, {
        temperature: 0.8,
        max_tokens: 200
      });
    } catch (error) {
      console.error('AI:', error);
      onChunk(this.getFallbackResponse(userMessage));
    }
  }

  private getFallbackResponse(userMessage: string): string {
    const lowerMessage = userMessage.toLowerCase();
    
    if (lowerMessage.includes('') || lowerMessage.includes('')) {
      const responses = {
        strict: '5',
        gentle: '',
        humorous: ''
      };
      return responses[this.coachPersonality];
    }
    
    if (lowerMessage.includes('') || lowerMessage.includes('')) {
      const responses = {
        strict: '',
        gentle: '',
        humorous: '""'
      };
      return responses[this.coachPersonality];
    }
    
    const defaultResponses = {
      strict: '',
      gentle: '',
      humorous: ''
    };
    
    return defaultResponses[this.coachPersonality];
  }
}