//  - 
// MongoDB

export interface DatabaseStats {
  users: number;
  checkIns: number;
  contracts: number;
  aiSessions: number;
  timestamp: Date;
}

export interface User {
  _id: string;
  username: string;
  email: string;
  profile?: {
    name?: string;
    age?: number;
    height?: number;
    weight?: number;
    goal?: string;
  };
  createdAt: Date;
  updatedAt: Date;
}

export interface CheckInRecord {
  _id: string;
  userId: string;
  type: 'breakfast' | 'lunch' | 'dinner' | 'workout' | 'protein';
  image?: string;
  location?: {
    latitude: number;
    longitude: number;
    address?: string;
  };
  aiAnalysis?: {
    foodItems?: string[];
    healthScore?: number;
    calories?: number;
    nutritionInfo?: {
      protein?: number;
      carbs?: number;
      fat?: number;
      fiber?: number;
    };
    recommendations?: string[];
    description?: string;
  };
  timestamp: Date;
  createdAt: Date;
}

// 
let mockData = {
  users: [] as User[],
  checkIns: [] as CheckInRecord[],
  contracts: [] as any[],
  aiSessions: [] as any[],
  isConnected: false,
  connectionTime: null as Date | null,
};

/**
 * 
 */
export async function connectToMockDatabase(): Promise<{ success: boolean; message: string }> {
  // 
  await new Promise(resolve => setTimeout(resolve, 1000 + Math.random() * 2000));
  
  // 90%
  const success = Math.random() > 0.1;
  
  if (success) {
    mockData.isConnected = true;
    mockData.connectionTime = new Date();
    return {
      success: true,
      message: ' MongoDB'
    };
  } else {
    mockData.isConnected = false;
    return {
      success: false,
      message: ' : '
    };
  }
}

/**
 * 
 */
export async function checkMockDatabaseHealth(): Promise<boolean> {
  // 
  await new Promise(resolve => setTimeout(resolve, 500));
  
  return mockData.isConnected && Math.random() > 0.05; // 95%
}

/**
 * 
 */
export async function getMockDatabaseStats(): Promise<DatabaseStats> {
  // 
  await new Promise(resolve => setTimeout(resolve, 300));
  
  if (!mockData.isConnected) {
    throw new Error('');
  }
  
  return {
    users: mockData.users.length,
    checkIns: mockData.checkIns.length,
    contracts: mockData.contracts.length,
    aiSessions: mockData.aiSessions.length,
    timestamp: new Date(),
  };
}

/**
 * 
 */
export async function initializeMockDatabase(): Promise<void> {
  // 
  await new Promise(resolve => setTimeout(resolve, 1500));
  
  if (!mockData.isConnected) {
    throw new Error('');
  }
  
  // 
  console.log(' ...');
  await new Promise(resolve => setTimeout(resolve, 800));
  
  // 
  if (mockData.users.length === 0) {
    console.log(' ...');
    await createMockSampleData();
  }
  
  console.log(' ');
}

